<template>
    <span>
            <AppBarPage>
      

            </AppBarPage>

            <div style="height: 12px;"></div>

            <van-field
                v-model="fieldValue"
                is-link
                readonly
                label="工厂节点"
                placeholder="请选择工厂节点"
                type="textarea"
                autocomplete="off"
                required
                @click="show = true"
            />
            <van-popup v-model:show="show" round position="bottom">
                <v-btn
                    block
                    color="cyan"
                    elevation="0"
                    style="border-radius: 0px;"
                    @click="()=> show=false"
                >
                    确定
                </v-btn>
                <van-cascader
                    v-if="showFactory"
                    title="请选择工厂节点"
                    :options="options"
                    active-color="#4CAF50"
                    :field-names="{text:'nodeLevelName',value:'tmBasNodeLevelId',children:'children'}"
                    @close="show = false"
                    @change="onFinish"
                />
            </van-popup>

            <SelectComponents 
                v-model="supplier"
                ref="select678"
                label="供应商"
                required
                :option="supplierSelectOption"
            />

            <SelectComponents 
                v-model="part"
                ref="select567"
                label="零件"
                required
                showSearch
                :option="partSelectOption"
                @onSearchChange="partSearchChange"

            />

            <SelectComponents 
                v-model="process"
                ref="select22"
                label="过程类型"
                :option="processSelectOption"
            />

            <SelectComponents 
                v-model="typeMaterial"
                ref="select663"
                label="来料类型"
                required
                :option="typeMaterialSelectOption"
            />



            <SelectComponents 
                v-model="classes"
                ref="select33"
                label="班次"
                
                :option="classesSelectOption"
            />

            <van-field v-model="taskQty" placeholder="请输入" required autocomplete="off" type="number" label="任务数量"  />
            <van-field v-model="receiptNo" placeholder="请输入" required autocomplete="off" label="入库单编号"  />
            <van-field v-model="receiptline" placeholder="请输入" required autocomplete="off" label="入库单序号"  />
            <van-field v-model="receiptQty" placeholder="请输入" required autocomplete="off" type="number" label="收货数量"  />



            <v-row no-gutters>
                <v-col cols="4">
                    <p style="padding-left:16px;margin-top:14px;">是否紧急</p>
                </v-col>
                <v-col cols="8" style="height: 42px;">
                    <v-switch
                    v-model="switchUrgency"
                    :label="switchUrgency?'是':'否'"
                    color="primary"
                    density="comfortable"
                    style="height:48px;display:inline-block;height:48px;width:104px;margin-left:14px;"
                    ></v-switch>
                </v-col>
            </v-row>

            <v-row no-gutters>
                <v-col cols="4">
                    <p style="padding-left:16px;margin-top:14px;">是否已收货</p>
                </v-col>
                <v-col cols="8" style="height: 42px;">
                    <v-switch
                    v-model="switchReceived"
                    :label="switchReceived?'是':'否'"
                    color="primary"
                    density="comfortable"
                    style="height:48px;display:inline-block;height:48px;width:104px;margin-left:14px;"
                    ></v-switch>
                </v-col>
            </v-row>

            <v-row no-gutters>
                <v-col cols="4">
                    <p style="padding-left:16px;margin-top:14px;">是否外检</p>
                </v-col>
                <v-col cols="8" style="height: 42px;">
                    <v-switch
                    v-model="switchOutInspect"
                    :label="switchOutInspect?'是':'否'"
                    color="primary"
                    density="comfortable"
                    style="height:48px;display:inline-block;height:48px;width:104px;margin-left:14px;"
                    ></v-switch>
                </v-col>
            </v-row>



            <div style="margin-top:32px;margin-bottom: 62px;">
                <v-row no-gutters>
                    <!-- <v-col cols="4" class="text-center">
                        <v-btn @click="()=> drawer=false " density="compact"  variant="plain">关闭</v-btn>
                    </v-col> -->
                    <v-col cols="6" class="text-center">
                        <v-btn @click="resetClick" color="warning" >重置</v-btn>
                    </v-col>
                    <v-col cols="6" class="text-center">
                        <v-btn @click="searchClick" color="primary" >提交</v-btn>
                    </v-col>
                </v-row>
            </div>
            


    </span>
</template>
<script>
    import AppBarPage from '@/components/AppBar.vue'

    import SelectComponents from '@/packages/Select.vue'
    import {httpHandle} from '@/http/http'  // api
    import { showSuccessToast,showFailToast } from 'vant'


    import {FactoryTreeHTTP} from '@/http/equipment/repairs'   // api
    import {FormatTree} from '@/utils/data'   // utils
    import {RepairmanHTTP} from '@/http/equipment/repairs'   // api

  export default {
    components:{
        AppBarPage,
        SelectComponents
    },
    emits: ["searchHandle","resetHandle"],
    data: () => ({
        drawer: false,
        showFactory:true,  // 重置工厂

        show:false,   // 工厂 show
        fieldValue:"",  // 工厂显示值
        options:[],    // 工厂 数据

        factoryID:"",   // 工厂选中ID

        process:"",   // 过程类型 
        processSelectOption:[],   // 过程类型 数据

        typeMaterial:"",     // 来料类型
        typeMaterialSelectOption:[],    // 来料类型 数据

        switchUrgency:false,   // 是否紧急
        switchReceived:false,   // 是否已收货
        switchOutInspect:false,   // 是否外检


        classes:"",     // 班次
        classesSelectOption:[],   // 班次 数据

        taskQty:"",   // 任务数量
        receiptNo:"",   // 收货单号
        receiptline:"",   // 收货单行号
        receiptQty:"",   // 收货数量



        supplier:"",   // 供应商
        supplierSelectOption:[],   // 供应商 数据

        part:"",   // 零件
        partSelectOption:[],   // 零件 数据

    }),
    created(){
        this.initFunc()

        this.getSupplierHttp()
        this.partHttp()
    },
    methods: {
        // 数据字典 格式化
        FormatDictionary(key="",valueKey=""){
            const _bufferDictionaries=JSON.parse(localStorage.getItem("bufferDictionaries")||"{}")
            const _obj=(_bufferDictionaries[key] || [] )
            const _option=_obj.filter(o=>valueKey==o.value)[0]||{}
            return _option
        },
        // 重置
        resetClick(){
            this.factoryID=""    //工厂节点，选择值
            this.part=""     //零件，选择值
            this.supplier=""     //供应商，选择值
            this.process=""     //过程类型，数据字典 QC_TASK_TYPE，选择值
            this.typeMaterial=""   //  来料检：in_part_model，字段partModel

            this.switchUrgency=false     //是否紧急，数据字典 yes_no，选择值
            this.classes=""     //班次，数据字典 shiftno，选择值
            this.taskQty=''     //任务数量，手动输入
            this.receiptNo=""     //收货单号，手动输入
            this.receiptline=""     //收货单行号，手动输入
            this.receiptQty=""     //收货数量，手动输入
            this.switchReceived=false    //是否已收货，数据字典 yes_no
            this.switchOutInspect=false     //是否外检，数据字典 yes_no


            this.$refs.select678.reset()
            this.$refs.select567.reset()
            this.$refs.select22.reset()
            this.$refs.select663.reset()

            
            this.$refs.select33.reset()

            this.fieldValue=""
            this.showFactory=false
            this.$nextTick(()=>{
                this.showFactory=true
            })
            // this.$emit("resetHandle",{})


        },
        // 初始化 下拉框
        async initFunc(){
            // 数据字典
            // 属性   abnormal_type
            // 状态     abnormal_state
            // 紧急程度   urgent_degree

            const _bufferDictionaries=JSON.parse(localStorage.getItem("bufferDictionaries")||"{}")


            // 过程类型
            const _processAttribute=_bufferDictionaries["QC_TASK_TYPE"]||[]   
            this.processSelectOption=_processAttribute.map(o=>Object.assign({text:o.lable,value:o.value}))   // 属性数据
            

            // 来料类型
            const _typeMaterialAttribute=_bufferDictionaries["in_part_model"]||[]   
            this.typeMaterialSelectOption=_typeMaterialAttribute.map(o=>Object.assign({text:o.lable,value:o.value}))   // 属性数据
            
            


            // 班次
            const _classesAttribute=_bufferDictionaries["shiftno"]||[]   
            this.classesSelectOption=_classesAttribute.map(o=>Object.assign({text:o.lable,value:o.value}))   // 属性数据
      
            

            // 工厂节点数据
            const {data=[]} = await FactoryTreeHTTP()
            const _tree=FormatTree(data,"tmBasNodeLevelId", "parentId")
            this.options=_tree
        },
        // 供应商
        async getSupplierHttp(key=""){
            
            // 展示  abnormalNo + abnormalName   
            // 取值  tmBasAbnormalTypeId
            const {code,data=[]}= await httpHandle({
                url:'/iiot/supplier/list?delFlag=Y',
                method:"get",
                // url_params:{
                //     // ttQmAbnormalId:ttQmAbnormalId
                //     abnormalNo:key
                // }
    
            }) 

            if(code==200){
                this.supplierSelectOption=data.map(o=>Object.assign({
                    text:`${o.supplierName}`,
                    value:o.supplierCode
                }))  
            }
        },   
        // 零件
        async partHttp(key=""){
            
            // 展示  abnormalNo + abnormalName   
            // 取值  tmBasAbnormalTypeId
            const {code,data=[]}= await httpHandle({
                url:'/iiot/part/listPartForSelect',
                method:"get",
                url_params:{
                    noOrName:key
                }
    
            }) 

            if(code==200){
                this.partSelectOption=data.map(o=>Object.assign({
                    text:`${o.matnr||''}-${o.maktxC||''}`,
                    value:o.tmBasPartId
                })).splice(0,100)  
            }
        },   
        // 零件切换
        partSearchChange(key){
            this.partHttp(key)
        },
        // 工厂 完成
        onFinish ({ selectedOptions }){


            if(!selectedOptions.length) return

            // tmBasNodeLevelId:“465621691089092608
            let _tmBasNodeLevelId= (selectedOptions[ selectedOptions.length-1 ]||{})["tmBasNodeLevelId"]
            
            this.factoryID=_tmBasNodeLevelId
            this.fieldValue = selectedOptions.map((o) => o.nodeLevelName).join('/')
        },
        // 查询
        async searchClick(){

            if(!this.factoryID){
                showFailToast("工厂必填！")
                return
            }

            if(!this.supplier){
                showFailToast("供应商必填！")
                return
            }

            if(!this.part){
                showFailToast("零件必填！")
                return
            }

            if(!this.typeMaterial){
                showFailToast("来料类型必填！")
                return
            }

            if(!Number(this.taskQty)){
                showFailToast("任务数量必填！")
                return
            }

            if(!this.receiptNo.trim()){
                showFailToast("收货单号必填！")
                return
            }

            if(!this.receiptline.trim()){
                showFailToast("收货单行号必填！")
                return
            }

            if(!Number(this.receiptQty)){
                showFailToast("收货数量必填！")
                return
            }
            
            const _json={
                "taskType": "IQC",   // 检验类型，固定   IQC
                "tmBasNodeLevelId": this.factoryID,     //工厂节点，选择值
                "tmBasPartId": this.part,     //零件，选择值
                "tmBasSupplId": this.supplier,     //供应商，选择值
                "processType": this.process,     //过程类型，数据字典 QC_TASK_TYPE，选择值
                "partModel": this.typeMaterial,  // 来料类型
                "isUrgent": this.switchUrgency?'1':'0',     //是否紧急，数据字典 yes_no，选择值
                "shiftno": this.classes,     //班次，数据字典 shiftno，选择值
                "taskQty": Number(this.taskQty),     //任务数量，手动输入
                "receiptNo": this.receiptNo,     //收货单号，手动输入
                "receiptline": this.receiptline,     //收货单行号，手动输入
                "receiptQty": Number(this.receiptQty),     //收货数量，手动输入
                "isReceipt": this.switchReceived?'1':'0',     //是否已收货，数据字典 yes_no
                "isOutInspect": this.switchOutInspect?'1':'0',     //是否外检，数据字典 yes_no
            }


            const {code,data={}}= await httpHandle({
                url:'/iiot/qmTask',
                method: "post",
                payload:_json
            })

            if(code==200){
                showSuccessToast("提交成功！")
                this.$router.push({
                    path:'/qualityMaterial/index', 
                    query:{ }
                }) 
            }



        },
        showDrawer(){
            this.drawer=true
        }
    },
    props: {

    }
  }
</script>